@use 'sass:color';

// Variables
$color__white: #fff;
$color__black: #000;

$color__gray-100: #f1f1f1;
$color__gray-200: #d4d0cb;
$color__gray-300: #c5c5c5;
$color__gray-400: #808081;
$color__gray-500: #d4d0cb;
$color__gray-800: #555;
$color__gray-900: #333;

$color__red: #ed392a; // Whenever possible, "China" data should be in this color.
$color__dark-red: #c42d21;
$color__green: #58a897;
$color__bright-green: #8cb561;
$color__light-blue: #83badc;
$color__blue: #3b75bb;
$color__purple: #a483a8;
$color__dark-purple: #69518d;
$color__orange: #f7890e;
$color__yellow: #e7ae3f;
$color__blue-gray-100: #728c99;
$color__blue-gray-200: #455a64;
$color__blue-gray-900: #303f46;

$color__primary: $color__blue-gray-200;
$color__primary-200: color.scale($color__primary, $lightness: -10%);

/*----------  Fonts  ----------*/
$font__hind: 'Hind', sans-serif;
$font__roboto: 'Roboto', sans-serif;

/*----------  Structural  ----------*/
$breakpoints: (
  'small': 0,
  'medium': 48em,
  'large': 64em,
  'xlarge': 90em,
);

$size__max-width: 700px;
$size__content-max-width: 900px;

